@import "../../Styles/sass/variables.scss";

.isClose {
    opacity: 0;
}

.closeElement {
    width: 6px;
    border-radius: 2px;
    height: 150px;
    background-color: $bgColor;
    margin-right: 10px;
    cursor: pointer;

    &:active {
        translate: 5px;
    }
}

.isActive {
    width: 100vw;
    height: 100vh;
    background-color: rgb(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    opacity: 1;
    transition: all 0.7s;
    ;

    .modalContent {
        height: 100%;
        width: 500px;
    }
}